.container{
    display: flex;
    overflow: hidden;
    
}


.videos{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background-color: antiquewhite;
   

}
.video__map{
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
    
}
.video__lists{
cursor: pointer;
display: flex;
flex-direction: column;
margin-top: -31rem;
font-size: 20px;
padding: 5px;

}
.video__list{
   padding: 20px 
}
.video__title{
    font-size: 20px;
    font-weight: bolder;
    margin-top: 0;
    padding: 5px;

}
.video__desc{
    width: 50%;
    height: 8vh;
    background-color: rgb(189, 185, 185);
    border: #ddd;
    border-radius: 5px;
    margin:0px;
    padding:0px;
    bottom: 0px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

}
.course{
    font-size: 20px;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.course__videos{
    font-size: 20px;
    color: blue;
    margin: 0px;
    padding: 0px;
    cursor: pointer;

}


.video__details{
    display: flex;
    flex-direction: column;
}

video-player {
    position: relative;
  }
  
  .percentage-bar {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100px;
    height: 100px;
    position: fixed;
    
  }
  
  .percentage-bar-outer {
    width: 43px;
    height: 43px;
    border-radius: 50%;
    border: 2px solid #ddd;
    box-sizing: border-box;
    margin-left: 38rem;
   margin-top: -16rem;
   cursor: pointer;

  }
  
  .percentage-bar-inner {
    position: absolute;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    transform-origin: bottom center;
    border-right: 2px solid rgb(110, 110, 202);
    border-top: 2px solid rgb(125, 125, 221);
    transform: rotate(0deg);
    transition: transform 0.3s ease-out;
    cursor: pointer;
  }
  .hr__bar{
    width: 100%;
    
   

  }
  
  

  